  import BScroll from 'better-scroll'
  export default {
    data () {
      return {
        majorScroll: null
      }
    },
    // name: 'aboutus',
    mounted () {
      this.$nextTick(() => {
        this.majorScroll = new BScroll(this.$refs.aboutus, {
          click: true
        })
      })
    },
    methods: {
      gotoBack () {
        this.$router.go(-1)
      },
      gotoLoad () {
        this.majorScroll && this.majorScroll.refresh()
      },
    }
}



